import {
	uniCard,
	uniIcons,
	uniPagination,
	uniList,
	uniPopup,
	uniListItem,
	uniNavBar
} from '@dcloudio/uni-ui';
// 也可使用此方式引入组件

import {
	mapState,
	mapMutations
} from 'vuex';

export default {
	components: {
		uniCard,
		uniList,
		uniPopup,
		uniListItem,
		uniIcons,
		uniPagination,
		uniNavBar
	},

	computed: {
		...mapState(['hasLogin'])
	},

	onLoad() {
		const userId = uni.getStorageSync('userId') || '';
		if (!userId) {






			this.getOpenId();

		} else {
			this.Token = userId;
		}
		this.navs = [...this.Navs.navs];
		const myDate = new Date();
		this.nowdate = {
			year: String(myDate.getFullYear()),
			month: String(Number(myDate.getMonth() + 1) < 10 ? '0' + (myDate.getMonth() + 1) : myDate.getMonth() + 1),
			day: String(Number(myDate.getDate() + 1) < 10 ? '0' + myDate.getDate() : myDate.getDate())
		}
	},
	onShow() {
		const userId = uni.getStorageSync('userId') || '';
		if (userId) {
			console.log('userId:', userId)
			this.getSummary();
			this.getData();
		}
	},
	data() {
		return {
			navs: [],
			Token: '',
			lists: [],
			budget: '',
			imgUrl: "",
			pageNum: 0,
			opearObj: {},
			isnull: false,
			iseye: false,
			isbudget: false,
			isoperat: false,
			nowdate: {
				year: "",
				month: "",
				day: "",
				startTime: "",
				endTime: ""
			},
			summary: {
				budgetotal: 0, //当月预算金额
				budgetId: '', // 当月预算金额数据ID
				exAmount: 0, //当月支出总额
				inAmount: 0 //当月收入总额
			}
		}
	},
	methods: {

		...mapMutations(['login']),
		// 获取微信openid
		getOpenId() {
			uni.login({
				provider: 'weixin',
				success: loginRes => {
					uniCloud.callFunction({
						name: 'getUnionid',
						data: {
							js_code: loginRes.code
						}
					}).then((res) => {
						this.wxLogin(res.result.data.openid)
					})
				}
			});
		},
		// 微信登录
		wxLogin(openid) {
			const _this = this;
			uniCloud.callFunction({
				name: 'login',
				data: {
					openid: openid
				}
			}).then((res) => {
				console.log('res:', res)
				if (res.result.status == 0) {
					this.login(res.result.token);
					this.Token = res.result.token;
					this.getSummary();
					this.getData();
				}
				if (res.result.status == 1) {
					uni.reLaunch({
						url: './login?id='+res.result.token
					})
				}
			})
		},

		// 获取汇总数据
		getSummary() {
			console.log('getSummary:')
			uni.showLoading({
				mask: true,
				title: "数据加载中..."
			})
			const dataobj = {
				range: "month",
				token: this.Token,
				yearMonth: this.nowdate.year + '-' + this.nowdate.month,
				year: this.nowdate.year,
				month: this.nowdate.month
			}
			uniCloud.callFunction({
				name: 'summary',
				data: dataobj
			}).then((res) => {
				uni.hideLoading();
				this.summary = res.result;
				this.budget = res.result.budgetotal;
			}).catch((err) => {
				uni.hideLoading();
				uni.showModal({
					content: `查询失败，错误信息为：${err.message}`,
					showCancel: false
				})
				console.error(err)
			})
		},
		//获取列表数据
		getData() {
			console.log('getData')
			uni.showLoading({
				mask: true,
				title: "数据加载中..."
			})
			const _data = {
				token: this.Token,
				pageNum: this.pageNum,
				range: 'day',
				year: this.nowdate.year,
				month: this.nowdate.month,
				day: this.nowdate.day
			}
			uniCloud.callFunction({
				name: 'get',
				data: _data
			}).then((res) => {
				uni.hideLoading();
				let extotal = 0,
					intotal = 0;
				if (res.result.total > 0) {
					this.isnull = false;
					res.result.list.forEach(el => {
						el.weekday = this.utils.getMyDay(el.useDate);
						if (el.genre == 1) {
							extotal += Number(el.amount)
						} else if (el.genre == 2) {
							intotal += Number(el.amount)
						}
					})
					let obj = {
						datetime: `${this.nowdate.year}-${this.nowdate.month}-${this.nowdate.day}`,
						budget: `收入：${intotal} 支出：${extotal}`,
						listItem: res.result.list
					}
					this.lists = [];
					this.lists.push(obj);
				} else {
					this.isnull = true;
				}
			}).catch((err) => {
				uni.hideLoading();
				this.lists = [];
				this.isnull = true;
				uni.showToast({
					icon: 'none',
					mask: true,
					title: '未查询到相关信息'
				})
			})
		},
		// 设置预算 
		setbudget() {
			this.isbudget = true;
			this.isoperat = false;
			this.$refs.indpopup.open()
		},
		// 提交预算
		submitBudget() {
			uni.showLoading({
				mask: true,
				title: '处理中...'
			})
			let nowmonth = this.nowdate.year + '-' + this.nowdate.month;
			let eventName = "",
				postData = {
					yearMonth: nowmonth,
					createTime: new Date(nowmonth).getTime(),
					budgetTotal: this.budget,
					token: this.Token,
				};

			if (this.summary.budgetId) {
				eventName = "updateBudget";
				postData.budgetId = this.summary.budgetId;
			} else {
				eventName = "budget";
			}

			uniCloud.callFunction({
				name: eventName,
				data: postData
			}).then((res) => {
				uni.hideLoading()
				this.$refs.indpopup.close();
				this.isbudget = false;
				uni.showModal({
					content: '设置成功',
					showCancel: false
				})
				this.getSummary();
			}).catch((err) => {
				this.$refs.indpopup.close();
				this.isbudget = false;
				uni.hideLoading()
				uni.showModal({
					content: '设置失败，请重新设置',
					showCancel: false
				})
				console.error(err)
			})
		},
		// 点击顶部搜索按钮
		clickSearch() {
			this.$refs.navs.close();
			this.$refs.picture.close();
			this.$refs.indpopup.close();
			uni.navigateTo({
				url: './search'
			});
		},
		//  点击某条记录
		handleItem(Id) {
			uni.navigateTo({
				url: './details?id=' + Id
			});
		},
		// 长按某条记录，选择下一步操作
		handleLong(item) {
			this.opearObj = item;
			this.isoperat = true;
			this.isbudget = false;
			this.$refs.indpopup.open();
		},
		// 查看凭证图片
		onshowproof(url) {
			this.imgUrl = url;
			this.$refs.picture.open();
		},
		// 修改1 删除 2
		moreOper(val) {
			const _this = this;
			if (val == 1) {
				this.handleItem(this.opearObj._id)
			} else {
				uni.showModal({
					title: '提示',
					content: '是否确认删除此条数据？',
					success: function(res) {
						if (res.confirm) {
							uni.showLoading({
								title: "数据删除中..."
							})
							uniCloud.callFunction({
								name: 'remove',
								data: {
									token: _this.Token,
									dataId: _this.opearObj._id
								}
							}).then((res) => {
								uni.hideLoading();
								uni.showToast({
									title: res.result.msg,
									icon: 'none',
									duration: 2000
								});
								_this.getData();
							}).catch((err) => {
								uni.hideLoading();
								uni.showModal({
									content: `操作失败，请重新操作`,
									showCancel: false
								})
								console.error(err)
							})

						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}
			this.isoperat = false;
			this.$refs.indpopup.close();
		},
		//点击菜单按钮
		openNavs() {
			this.$refs.navs.open();
		},
		// 点击某个菜单跳转到相应页面
		handleNav(item) {
			this.$refs.navs.close();
			if (item.id == 1) {
				uni.navigateTo({
					url: './about'
				});
			} else if (item.id == 2) {
				uni.navigateTo({
					url: './site'
				});
			} else if (item.id == 3) {
				uni.navigateTo({
					url: './annal'
				});
			} 
		}
	}
}
